import * as THREE from 'three';

export default class Particles {
    constructor(_option) {
        this.time = _option.time;
        this.materials = _option.materials;

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        this.setParticles();
    }

    setParticles() {
        const parameters = { 
            count: 1000,           // 
            size: 0.01,              // 
            radius: 5,               // 
            branches: 3,             // 
            randomness: 0.2,         // 
            randomnessPower: 3,      //  ()
            insideColor: '#ff6030',  // 
            outsideColor: '#1b3984', // 
        }

        this.geometry = new THREE.BufferGeometry();

        const positions = new Float32Array(parameters.count * 3)
        const colors = new Float32Array(parameters.count * 3)
        const scales = new Float32Array(parameters.count * 1)
        const randomness = new Float32Array(parameters.count * 3)
        const colorInside = new THREE.Color(parameters.insideColor)
        const colorOutside = new THREE.Color(parameters.outsideColor)

            //  position, color
    for(let i = 0; i < parameters.count; i++) {
        const i3 = i * 3
        //  0 ~ radius 
        const radius = Math.random() * parameters.radius
        //  (3  0,-120,+120 )
        const branchAngle = (i % parameters.branches) / parameters.branches * Math.PI * 2
        // 
        const randomX = Math.pow(Math.random(), parameters.randomnessPower) * (Math.random() < 0.5 ? 1 : - 1) * parameters.randomness * radius
        const randomY = Math.pow(Math.random(), parameters.randomnessPower) * (Math.random() < 0.5 ? 1 : - 1) * parameters.randomness * radius
        const randomZ = Math.pow(Math.random(), parameters.randomnessPower) * (Math.random() < 0.5 ? 1 : - 1) * parameters.randomness * radius
        // 
        positions[i3    ] = Math.cos(branchAngle) * radius 
        positions[i3 + 1] = 0
        positions[i3 + 2] = Math.sin(branchAngle) * radius 
        // 
        const mixedColor = colorInside.clone()
        mixedColor.lerp(colorOutside, radius / parameters.radius)
        colors[i3    ] = mixedColor.r
        colors[i3 + 1] = mixedColor.g
        colors[i3 + 2] = mixedColor.b
        // 
        scales[i] = Math.random()
        //  ( shader )
        randomness[i3    ] = randomX
        randomness[i3 + 1] = randomY
        randomness[i3 + 2] = randomZ
    }

    this.geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3))
    this.geometry.setAttribute('color', new THREE.BufferAttribute(colors, 3))
    this.geometry.setAttribute('aScale', new THREE.BufferAttribute(scales, 1))
    this.geometry.setAttribute('aRandomness', new THREE.BufferAttribute(randomness, 3))

        this.material = this.materials.items.shader.particles;

        this.instance = new new THREE.Points(this.geometry, this.material);
        this.container.add( this.instance );
    }
}